* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow: hidden;
  text-align: center;
  overflow: auto;
}

body {
  width: 100%;
  height: 100vh;
  text-align: center;
}

.hidden {
  display: none;
}

#graph {
  width: 100%;
  height: calc(100vh - 60px);
  position: absolute;
  overflow: hidden;
}

#home {
  width: 100%;
}
